.project__space{
  margin-top: 80px;
}
.project__section {
  padding: 10px 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #151619;
}
.project__container-card {
  background: #fafafa;
  box-shadow: 0 6px 20px #151619;
  width: 280px;
  height: 500px;
  text-decoration: none;
  border-radius: 4px;
}

/* .project__container-card:nth-child(2) {
  transform: scale(1.05);
  background: #242424;
} */

.project__container-card:hover {
  transform: scale(1.06);
  transition: all 0.3s ease-out;
  color: #1c2237;
}

.project__container-card:nth-child(2) {
  margin: 0 24px;
}

.project__container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project__container-two {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project__wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin: 0 auto;
}

.project__heading {
  color: #fafafa;
  margin-bottom: 24px;
}

.project__container-cardInfo {
  display: flex;
  flex-direction: column;
  height: 500px;
  padding: 5px;
  align-items: center;
  color: #151619;
}

.img__container{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.project__container-cardInfo h3 {
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 24px;
}

.project__container-cardInfo h4 {
  font-size: 40px;
}

.project__container-cardInfo p {
  font-size: 14px;
  margin-bottom: 24px;
}

.project__container-features {
  margin: 16px 0 32px;
  padding: 20px;
  list-style: none;
}

.project__container-features li {
  margin-bottom: 10px;
}

.project__container-features {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  margin: 24px 0;
}

@media screen and (max-width: 960px) {
  .project__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .project__container-card {
    width: 90%;
  }

  .project__wrapper {
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .project__container-card:nth-child(2) {
    transform: scale(1);
    background: #fafafa;
    margin: 24px;
  }

  .project__container-card:hover {
    transform: scale(1.06);
  transition: all 0.3s ease-out;
  }
}
